import { useState } from 'react'
import AddTodoItem from './AddTodoItem'
import uuid from 'react-uuid'

const AddTodoComponent = ({ writeData, setFilesForSend, filesForSend }) => {
  /**
   *      
   */
  const [title, setTitle] = useState('')
  const [description, setDescription] = useState('')
  const [endDate, setEndDate] = useState('')

  /**
   *     
   */
  const [emptyFields, setEmptyFields] = useState([])

  /**
   * @description          ""    ,           
   */
  const addTodo = () => {
    const emptyFields = []
    if (!title) emptyFields.push('')
    if (!description) emptyFields.push('')
    if (!endDate) emptyFields.push('')
    setEmptyFields(emptyFields)
    if (emptyFields.length === 0) {
      writeData(uuid(), title, description, endDate, filesForSend)
      setTitle('')
      setDescription('')
      setEndDate('')
      setFilesForSend([])
    }
  }

  return (
    <div className='add_todo'>
      <AddTodoItem
        name={''}
        title={title}
        setTitle={setTitle}
        emptyFields={emptyFields}
        setEmptyFields={setEmptyFields}
        width={'23%'}
      />
      <AddTodoItem
        name={''}
        title={description}
        setTitle={setDescription}
        emptyFields={emptyFields}
        setEmptyFields={setEmptyFields}
        width={'43%'}
      />
      <AddTodoItem
        name={''}
        title={endDate}
        setTitle={setEndDate}
        emptyFields={emptyFields}
        setEmptyFields={setEmptyFields}
        isDate={true}
      />
      <AddTodoItem
        name={' '}
        title={filesForSend}
        setTitle={setFilesForSend}
        isFile={true}
        width={'20%'}
        setFilesForSend={setFilesForSend}
        files={filesForSend}
      />
      <button onClick={addTodo}></button>
    </div>
  )
}

export default AddTodoComponent
